import DeriNumberFormat from "../../utils/DeriNumberFormat";
import DownLogo from "../../assets/img/pnlDown.svg";
import UpLogo from "../../assets/img/pnlUp.svg";
import classNames from "classnames";
import Icon from "../Icon/Icon";
import React from "react";
const Tip = ({
  index,
  market,
  active,
  payload,
  onChange,
  lastData,
}) => {
  if (active && payload && payload.length) {
    onChange(payload);
    return (
      <div className="tip-chart">
        <div className="price-chart-box">
          <div className="tip-index-price-pnl">
            <div className="tip-index-name">{index}</div>
            <div className="tip-index-price">
              <DeriNumberFormat
                value={payload[0].payload.entryPrice}
                decimalScale={2}
              />
            </div>
          </div>
          <div className="tip-market-price-pnl">
            <div className="tip-market-name">{market}</div>
            <div className="tip-market-price">
              <DeriNumberFormat
                value={payload[0].payload.curIndexPrice}
                decimalScale={2}
              />
            </div>
          </div>
        </div>
      </div>
    );
  } else if (lastData.indexPrice) {
    let arr = [];
    let payload = {
      payload: lastData,
    };
    arr.push(payload);
    onChange(arr);
    return null;
  } else {
    let arr = [];
    let payload = {
      payload: lastData,
    };
    arr.push(payload);
    onChange(arr);
    return null;
  }
};

export default Tip;
