import { useState, useEffect, useCallback } from "react";
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import Tip from './Tip'
export default function Chart({ arr, indexName = "Entry Price", marketName = "Cur. Price", isLong, onChange, lastData }) {
  const [off, setOff] = useState(0);
  const [data, setData] = useState(arr)
  const gradientOffset = () => {
    const dataMax = Math.max(...data.map((i) => i.pnl));
    const dataMin = Math.min(...data.map((i) => i.pnl));

    if (dataMax <= 0) {
      setOff(0)
      return 0;
    }
    if (dataMin >= 0) {
      setOff(1)
      return 1;
    }
    console.log(dataMax / (dataMax - dataMin))
    setOff(dataMax / (dataMax - dataMin))
  };
  useEffect(() => {
    setData(arr)
  }, [arr])
  useEffect(() => {
    if (data) {
      gradientOffset()
    }
  }, [data])
  return (data ?
    <ResponsiveContainer>
      <AreaChart data={data} >
        <CartesianGrid vertical={true} horizontal={false} stroke="#203B60" />
        <XAxis dataKey="curIndexPrice" axisLine={false} hide={true} tickLine={false} allowDataOverflow={false}
          tick={{ fill: '#fff', fontSize: '14' }} />
        <YAxis dataKey="pnl" tickLine={false} axisLine={false} hide={false} tick={{ fill: '#fff', fontSize: '14' }} />
        <Tooltip cursor={true} active={true} content={<Tip index={indexName} market={marketName} onChange={onChange} lastData={lastData} />} />
        <defs>
          <linearGradient id="splitColor" x1="0" y1="0" x2="0" y2="1">
            <stop offset={off} stopColor="#59AE99" stopOpacity={0.5} />
            <stop offset={off} stopColor="#CE5E5E" stopOpacity={0.5} />
          </linearGradient>
        </defs>
        <Area type="monotone" isAnimationActive={false} dataKey="pnl" stroke={!isLong ? "#CE5E5E" : "#59AE99"} fillOpacity={0.5} strokeWidth={2} fill="url(#splitColor)" />
      </AreaChart>
    </ResponsiveContainer>
    : null)
}